/** @format */

@import '@app/theme/variables.scss';

.contacts {
    &_row {
        align-items: center;
        
        &_info {
            width: 40%;

            h5 {
                text-transform: uppercase;
            }

            p {
                margin: 16px 0;
            }
        }

        &_form {
            width: 55%;
            padding: 16px;
            display: grid;
            grid-template-areas:
                'name email'
                'subject subject'
                'button .';
            gap: 12px;
            border-radius: 16px;
            background-color: $lightDark;
            box-shadow: 0 0 16px $lightDark;

            :nth-of-type(1) {
                grid-area: name;
            }

            :nth-of-type(2) {
                grid-area: email;
            }

            :nth-of-type(3) {
                grid-area: subject;
            }

            :nth-of-type(4) {
                grid-area: button;
            }
        }
    }

    @media only screen and (max-width: 768px) {
        &_row {
            flex-wrap: wrap;
            gap: 32px;

            &_info, &_form {
                width: 100%;
            }
        }
    }

    @media only screen and (max-width: 500px) {
        &_row {
            gap: 16px;

            &_form {
                width: 100%;
                grid-template-areas:
                'name'
                'email'
                'subject'
                'button';
            }
        }
    }
}
